import React, { Fragment, useEffect, useContext } from 'react';
import {Link} from 'react-router-dom';
import Repos from '../repos/Repos'; 
import Spinner from '../layout/Spinner';
import GithubContext from '../../context/github/githubContext';
const User = ({match}) => {
    
    const {getUser, loading, user, repos, getRepos} = useContext(GithubContext);

    useEffect(() => {
        getUser(match.params.login)
        getRepos(match.params.login)
        //eslint-disable-next-line
    }, []);

    const {name,avatar_url,location,bio,blog,login,html_url,followers,following,public_repos,public_gists,company,hireable} = user;

    if(loading) return <Spinner />
    else{
        return (
            <Fragment>
                <Link to="/" className="btn btn-light">Back to Search</Link>
                
                Hireable:{' '}
                {hireable ? (<i className="fas fa-check text-success" /> ) : (<i className="fas fa-times-circle text-danger" />) }

                <div className="card grid-2">
                    <div className="all-center">
                        <img src={avatar_url} className="round-img" style={{width: "150px",marginBottom:"10px"}} alt=""/>
                        <h2>{name}</h2>
                        <p>{location}</p>
                    </div>
                    <div>
                        {
                            bio && <Fragment>
                                <h3>Bio</h3>
                                <p>{bio}</p>
                            </Fragment>
                        }
                        <a href={html_url} className="btn btn-dark my-1">Visit Github Profile</a>
                        <ul>
                            <li>
                                {login && <Fragment>
                                    <strong>Username: </strong>{login}
                                </Fragment>}
                            </li>
                            <li>
                                {company && <Fragment>
                                    <strong>Company: </strong>{company}
                                </Fragment>}
                            </li>
                            <li>
                                {blog && <Fragment>
                                    <strong>Blog: </strong>{blog}
                                </Fragment>}
                            </li>
                        </ul>
                    </div>
                </div>
            
                <div className="card text-center">
                    <div className="badge badge-primary">Followers: {followers}</div>
                    <div className="badge badge-success">Following: {following}</div>
                    <div className="badge badge-danger">Public Repositories: {public_repos}</div>
                    <div className="badge badge-dark">Public Gists: {public_gists}</div>

                </div>
                <Repos repos={repos} />
            </Fragment>
        )
    }
}

export default User
